* {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-of-type) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.is-active {
  color: #00acdc;
  position: relative;
}

[data-qa="nav-hover"]:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  color: #060b35;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card:hover {
  transition: transform .3s ease-in-out;
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  margin: 0;
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-inscript {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.button:hover {
  color: #00acdc;
  cursor: pointer;
  background: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-of-type {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 46px 0;
  padding-block: 50px 72px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 1200px) {
  .catalog {
    padding-inline: 128px;
  }
}

@media (width >= 480px) and (width <= 767px) {
  .catalog {
    grid-template-columns: 1fr 1fr;
    gap: 46px 48px;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 46px 48px;
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 46px 48px;
  }
}
